
@import 'libs';
@import 'fonts';
@import 'colors';
// USER VARIABLES SECTION

$fontsize: 16px; // Body Font Size
$lineheight: 1.65; // Document Line Height
$systemfont: -apple-system, BlinkMacSystemFont, Arial, sans-serif; // System Font Family
$NeonGradient: linear-gradient(90deg, #2695ff 0%, #ff003c 50%, #2695ff 100%);
// GENERAL DOCUMENT STYLES
::placeholder {
	color: #666; }

::selection {
	background-color: $accent;
	color: #fff; }

input, textarea {
	outline: none;
	&:focus:required:invalid {
		border-color: red; }
	&:required:valid {
		border-color: green; } }

body {
	font-family: $RobotoRegular;
	font-size: $fontsize;
	line-height: $lineheight;
	color: $textcolor;
	min-width: 320px;
	position: relative;
	overflow-x: hidden;
	background-color: #2C313C; }
